$(document).ready(function () {
    //SideNav Menu
    function openNav() {
        document.getElementById("mySidenav").style.width = "50%";
    }

    function closeNav() {
        document.getElementById("mySidenav").style.width = "0";
    }

//3D ANIMATION
    var site = site || {};
    site.window = $(window);
    site.document = $(document);
    site.Width = site.window.width();
    site.Height = site.window.height();

    var Background = function() {

    };

    Background.headparticle = function() {
        /**
         * @return {undefined}
         */
        function FPSTest() {
            Background.camera.position.x += 1 * (-.5 * d - Background.camera.position.x);
            Background.camera.position.y += .5 * (.5 * factor - Background.camera.position.y);
            Background.camera.lookAt(Background.scene.position);
            Background.renderer.render(Background.scene, Background.camera);
        }
        if (!Modernizr.webgl) {
            alert("Your browser dosent support WebGL");
        }
        var functionOptimization;
        /** @type {number} */
        var d = 0;
        /** @type {number} */
        var factor = 0;
        /** @type {number} */
        var startex = site.Width / 2;
        /** @type {number} */
        var scrollBarStartY = site.Height / 2;
        Background.camera = new THREE.PerspectiveCamera(40, site.Width / site.Height, 1, 2e3);
        /** @type {number} */
        Background.camera.position.z = 300;
        Background.scene = new THREE.Scene;
        var manager = new THREE.LoadingManager;
        var systemGeometry = new THREE.Geometry;
        var systemMaterial = new THREE.ParticleBasicMaterial({
            // color : 0xff5e91, pink color
            color: 0x0a1429,
            size : 1.1
        });
        (new THREE.OBJLoader(manager)).load("face.obj", function(n) {
            n.traverse(function(object) {
                if (object instanceof THREE.Mesh) {
                    $(object.geometry.vertices).each(function() {
                        systemGeometry.vertices.push(new THREE.Vector3(7 * this.x + 100, 7 * this.y - 5, 7 * this.z));
                    });
                }
            });
            Background.scene.add(functionOptimization);
        });
        functionOptimization = new THREE.ParticleSystem(systemGeometry, systemMaterial);
        Background.renderer = new THREE.WebGLRenderer({
            alpha : true
        });
        Background.renderer.setSize(site.Width, site.Height);
        Background.renderer.setClearColor(0, 0);
        $(".particlehead").append(Background.renderer.domElement);
        $(".particlehead").on("mousemove", function(e) {
            /** @type {number} */
            d = (e.clientX - startex) / 2 - 300;
            /** @type {number} */
            factor = (e.clientY - scrollBarStartY) / 2;
        });
        site.window.on("resize", function() {
            /** @type {number} */
            startex = site.Width / 2;
            /** @type {number} */
            scrollBarStartY = site.Height / 2;
            /** @type {number} */
            Background.camera.aspect = site.Width / site.Height;
            Background.camera.updateProjectionMatrix();
            Background.renderer.setSize(site.Width, site.Height);
        });
        /**
         * @return {undefined}
         */
        Background.animate = function() {
            Background.ticker = TweenMax.ticker;
            Background.ticker.addEventListener("tick", Background.animate);
            FPSTest();
        };
        FPSTest();
        Background.animate();
    }

    Background.headparticle();
// Custom Browser Scroll Bar
// custom cursor
    var cursor = $(".custom-cursor"),
        follower = $(".follow-cursor");

    var posX = 0,
        posY = 0;

    var mouseX = 0,
        mouseY = 0;

    TweenMax.to({}, 0.016, {
        repeat: -1,
        onRepeat: function() {
            posX += (mouseX - posX) / 9;
            posY += (mouseY - posY) / 9;

            TweenMax.set(follower, {
                css: {
                    left: posX - 12,
                    top: posY - 12
                }
            });

            TweenMax.set(cursor, {
                css: {
                    left: mouseX,
                    top: mouseY
                }
            });
        }
    });

    $(document).on("mousemove", function(e) {
        mouseX = e.pageX;
        mouseY = e.pageY;
    });

    $("a").on("mouseenter", function() {
        cursor.addClass("active");
        follower.addClass("active");
    });
    $("a").on("mouseleave", function() {
        cursor.removeClass("active");
        follower.removeClass("active");
    });

});
/*Test menu*/

jQuery(document).ready(function($){
    var overlayNav = $('.cd-overlay-nav'),
        overlayContent = $('.cd-overlay-content'),
        navigation = $('.cd-primary-nav'),
        toggleNav = $('.cd-nav-trigger');

    //inizialize navigation and content layers
    layerInit();
    $(window).on('resize', function(){
        window.requestAnimationFrame(layerInit);
    });

    //open/close the menu and cover layers
    toggleNav.on('click', function(){
        if(!toggleNav.hasClass('close-nav')) {
            //it means navigation is not visible yet - open it and animate navigation layer
            toggleNav.addClass('close-nav');

            overlayNav.children('span').velocity({
                translateZ: 0,
                scaleX: 1,
                scaleY: 1,
            }, 500, 'easeInCubic', function(){
                navigation.addClass('fade-in');
            });
        } else {
            //navigation is open - close it and remove navigation layer
            toggleNav.removeClass('close-nav');

            overlayContent.children('span').velocity({
                translateZ: 0,
                scaleX: 1,
                scaleY: 1,
            }, 500, 'easeInCubic', function(){
                navigation.removeClass('fade-in');

                overlayNav.children('span').velocity({
                    translateZ: 0,
                    scaleX: 0,
                    scaleY: 0,
                }, 0);

                overlayContent.addClass('is-hidden').one('webkitTransitionEnd otransitionend oTransitionEnd msTransitionEnd transitionend', function(){
                    overlayContent.children('span').velocity({
                        translateZ: 0,
                        scaleX: 0,
                        scaleY: 0,
                    }, 0, function(){overlayContent.removeClass('is-hidden')});
                });
                if($('html').hasClass('no-csstransitions')) {
                    overlayContent.children('span').velocity({
                        translateZ: 0,
                        scaleX: 0,
                        scaleY: 0,
                    }, 0, function(){overlayContent.removeClass('is-hidden')});
                }
            });
        }
    });

    function layerInit(){
        var diameterValue = (Math.sqrt( Math.pow($(window).height(), 2) + Math.pow($(window).width(), 2))*2);
        overlayNav.children('span').velocity({
            scaleX: 0,
            scaleY: 0,
            translateZ: 0,
        }, 50).velocity({
            height : diameterValue+'px',
            width : diameterValue+'px',
            top : -(diameterValue/2)+'px',
            left : -(diameterValue/2)+'px',
        }, 0);

        overlayContent.children('span').velocity({
            scaleX: 0,
            scaleY: 0,
            translateZ: 0,
        }, 50).velocity({
            height : diameterValue+'px',
            width : diameterValue+'px',
            top : -(diameterValue/2)+'px',
            left : -(diameterValue/2)+'px',
        }, 0);
    }
});

$(document).ready(function () {
    var typed = new Typed("#typed", {
        strings: ['Web designer &amp; <br> front-end developer <br>' +
        '<span style="font-family: LibreBaskerville-Italic; color: #98a4b8;font-size: 28px;">Creative and passionate</span>'],
        typeSpeed: 80,
        backDelay: 1600,
        loop: true,
        contentType: 'html',
        loopCount: null,
        onReset: function(self) {},
        onDestroy: function(self) {}
    });
    // variables
    var $nav = $('nav');
    // fullpage customization
    $('#fullpage').fullpage({
        sectionSelector: '.vertical-scrolling',
        slideSelector: '.horizontal-scrolling',
        navigation: true,
        slidesNavigation: true,
        controlArrows: false,
        loopBottom: true,
        afterLoad: function(anchorLink, index) {

            if (index == 2) {
                typed.reset();
                // console.log('im in 2');

            }
            if(index == 4) {
                $('#fp-nav').hide();
            }
            if ((index == 2) || (index == 5) ) {
                $('.navbar-brand svg .st0').css({"fill":"#fff"});
            }
            else {
                $('.navbar-brand svg .st0').css({"fill":"#0A1529"});
            }
            if (index == 5){
                $('.menu-text').css({"color":"#98a4b8"});
            }
            else {
                $('.menu-text').css({"color":"#0a1529"});

            }
        },

        onLeave: function(index) {
            if(index == 2) {
                typed.destroy();
                // console.log('im not in 2');
            };
            if (index == 4) {
                $('#fp-nav').show();
            }
        }
    });
});